___CSS_LOADER_EXPORT___.push([module.id, `.tit{
    color: red;
    font-size: 2rem;
    padding-left: 40px;
  }
   .Nabvar {
      background-color: #fff;
      top: 0;
      color: black;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: fixed;
      z-index: 1;
    } 
   /* Estilos dropdown */
   .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-toggle {
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    padding: 10px;
    cursor: pointer;
  }
  
  .dropdown-menu {
    display: none;
    position: absolute;
    background-color: #fff;
    border: 1px solid #ccc;
    margin-left: 38px; 
    width: 200px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
  }
  
  .dropdown:hover .dropdown-menu {
    display: block;
  }
  
  .dropdown-menu a {
    display: block;
    padding: 6px;
    text-decoration: none;
    color: #333;
  }
  
  .dropdown-menu a:hover {
    background-color: #f0f0f0;
  }
  #dropdownMenuButton{
    margin-left:45px;
    /* margin-top: 15px; */
    font-size: 16px;
    border-radius: 0px;
    height: 70px;
    min-width: 60px;
    border-color: #fff;
    background-color: #fff;
    color: #666666;
  }
  
  
